import React from 'react';

import ModalOpenLink from 'components/ModalCustom/ModalOpenLink/ModalOpenLink';
import BlockSimple from 'components/BlockSimple/BlockSimple';
import FormProductInfo from 'components/Forms/ProductForms/FormProductInfo/FormProductInfo';
import FormProductImgs from 'components/Forms/ProductForms/FormProductImgs/FormProductImgs';
import ActionPrimary from 'components/Block/BlockActions/Actions/ActionPrimary';
import { useParams } from 'react-router-dom';

import { toast } from 'react-toastify';

import s from './Actions.module.scss';

const Actions = () => {
  const { id } = useParams();
  return (
    <>
      <ModalOpenLink
        modalContent={
          <BlockSimple title=" " iconId="edit" className={s.modalBlock} headerClassName={s.modalHeader}>
            <FormProductInfo edit id={id} />
          </BlockSimple>
        }
      >
        <ActionPrimary iconId="edit" title="" />
      </ModalOpenLink>

      <ModalOpenLink
        modalContent={
          <BlockSimple title=" " iconId="gallery" className={s.modalBlock} headerClassName={s.modalHeader}>
            <FormProductImgs id={id} />
          </BlockSimple>
        }
      >
        <ActionPrimary iconId="gallery" title=" " />
      </ModalOpenLink>

      <ModalOpenLink
        modalContent={
          <BlockSimple title=" " iconId="plus" className={s.modalBlock} headerClassName={s.modalHeader}>
            <FormProductInfo create />
          </BlockSimple>
        }
      >
        <ActionPrimary iconId="plus" title=" " />
      </ModalOpenLink>

      <ModalOpenLink
        modalContent={
          <BlockSimple title=" " iconId="copy" className={s.modalBlock} headerClassName={s.modalHeader}>
            <FormProductInfo copy id={id} />
          </BlockSimple>
        }
      >
        <ActionPrimary iconId="copy" title=" " />
      </ModalOpenLink>

      <ActionPrimary
        iconId="delete"
        title=""
        onClick={() => {
          // eslint-disable-next-line no-restricted-globals
          const response = confirm('    ?');
          if (!response) {
            toast.info(' ');
            return;
          }
          toast.success(' ');
        }}
      />
    </>
  );
};

export default Actions;
