import React from 'react';
import SvgIcon from 'components/SvgIcon/SvgIcon';

import s from './Status.module.scss';
const Status = ({ status }) => {
  const statusName = {
    success: '',
    pending: '',
    rejected: '',
    visible: '',
    hidden: '',
    available: ' ',
    notAvailable: '',
    awaitingOnStock: '',
    isCommission: '',
    isStandart: '',
    default: '',
  };
  const iconId = {
    success: 'success',
    pending: 'time',
    rejected: 'error',
    visible: 'visibility-on',
    hidden: 'visibility-off',
    available: 'success',
    notAvailable: 'clear',
    awaitingOnStock: 'time',
    isCommission: 'success',
    isStandart: 'success',
    default: 'clear',
  };

  const compStatus = statusName[status] && iconId[status] ? status : 'default';
  const className = [s.statusBox, s[compStatus]].join(' ');

  return (
    <div className={className}>
      <SvgIcon iconId={iconId[compStatus]} size="18px" />
      <span>{statusName[compStatus]}</span>
    </div>
  );
};

export default Status;
