import React from 'react';
// import { useBlock } from 'components/Block/BlockContext';

import DateInfo from './DateInfo/DateInfo';
import Status from './Status/Status';
import { useBlock } from 'components/Block/BlockContext';

import RowSimple from './RowSimple/RowSimple';

import s from './TopInfo.module.scss';

const TopInfo = () => {
  const {
    post,
    post: { createdBy, updatedBy, categoryInfo, availabilityInfo, priceInfo },
  } = useBlock();

  return (
    <>
      <div className={s.topInfo}>
        <RowSimple title="" data={<Status status={post?.approvedStatus} />} info="  " />

        <RowSimple title="SKU" data={post?.sku} />
        <RowSimple title="" data={post?.name} />
        <RowSimple title="" data={post?.brand} />

        <RowSimple title="" data={<DateInfo dateString={post?.createdAt} />} />
        <RowSimple
          title=" (ID, )"
          data={`${createdBy?.authorName || 'author'} (${createdBy?.authorId || '0000'}, ${createdBy?.authorType || 'vendor'})`}
        />
        <RowSimple
          title=""
          data={<Status status={post?.visibilityStatus ? 'visible' : 'hidden'} />}
          info="    "
        />

        <RowSimple title="" data={<DateInfo dateString={post?.updatedAt} />} />
        <RowSimple
          title=" (ID, )"
          data={`${updatedBy?.changedByAuthorName || 'author'} (${updatedBy?.changedByAuthorId || '0000'}, ${
            updatedBy?.changedByAuthorType || 'vendor'
          })`}
        />

        <RowSimple title="" data={`${priceInfo?.price} `} />
        <RowSimple title="" data={`${priceInfo?.cost} `} />
        <RowSimple title=", ID" data={`${priceInfo?.cashbackId} `} />
        <RowSimple title="" data={<Status status={priceInfo?.isCommission ? 'isCommission' : 'isStandart'} />} />
        <RowSimple title=", %" data={`${priceInfo?.commission} `} />
        <RowSimple title=", %" data={`${priceInfo?.sale} `} />
        <RowSimple title="" data={`${priceInfo?.currency} `} />

        <RowSimple title=" (ID)" data={`${categoryInfo?.section} (${categoryInfo?.sectionId})`} />
        <RowSimple title="  (ID)" data={`${categoryInfo?.parentCategory} (${categoryInfo?.parentCategoryId})`} />
        <RowSimple title=" (ID)" data={`${categoryInfo?.category} (${categoryInfo?.categoryId})`} />

        <RowSimple title="" data={`${post?.description || ''}`} />
        <RowSimple title=" " data={`${post?.innerComment}` || ''} info="       " />

        <RowSimple title="" data={`${availabilityInfo?.availabilityStatus} `} />

        <RowSimple title="  ..." />

        <RowSimple title=" ..." />
      </div>
    </>
  );
};

export default TopInfo;
